.modal-window {
  opacity: 0;
  pointer-events: none;
  transition: all cubic-bezier(0.39, 0.58, 0.57, 1) 0.2s;
  visibility: hidden;
}
@media screen and (prefers-reduced-motion: reduce) {
  .modal-window {
    transition: none;
  }
}
.modal-window.is-open {
  opacity: 1;
  pointer-events: auto;
  transition: all cubic-bezier(0.39, 0.58, 0.57, 1) 0.2s;
  visibility: visible;
}
@media screen and (prefers-reduced-motion: reduce) {
  .modal-window.is-open {
    transition: none;
  }
}
.modal-window__background {
  align-items: center;
  background-color: rgba(15, 39, 59, 0.6);
  bottom: 0;
  display: flex;
  justify-content: center;
  left: 0;
  overflow-y: auto;
  position: fixed;
  right: 0;
  top: 0;
  z-index: 5000200;
}
.modal-window__background--frozen {
  filter: blur(2px);
}
.modal-window__container {
  box-shadow: 0px 0px 22px rgba(0, 0, 0, 0.3);
  margin: 5px;
  max-height: 100vh;
  max-width: 450px;
  overflow-y: auto;
  position: relative;
  width: 100%;
}
.modal-window__container--wide {
  max-width: 640px;
}
.modal-window__content {
  background: #f1f1f1;
  overflow: hidden;
  padding: 40px 20px;
}
.modal-window__close-button {
  background: #f1f1f1;
  font-size: 20px;
  margin: 0;
  padding: 0;
  position: absolute;
  right: 10px;
  text-decoration: none;
  top: 10px;
}
.modal-window__header {
  margin-bottom: 40px;
}
.modal-window__headline {
  font-size: 32px;
}
.modal-window__footer {
  margin-top: 40px;
}

/* Fix compatibility between modal-window and Google Places autocomplete */
.pac-container {
  z-index: 5000300;
}